exports.push([module.id, "\n.uploader-ui {\r\n  padding: 25px;\r\n  margin: 40px auto 0;\r\n  font-size: 20px;\r\n  font-family: Microsoft YaHei;\r\n  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, .4);\r\n          box-shadow: 0 0 10px rgba(0, 0, 0, .4);\r\n  height: 200px;\r\n  width: 600px;\n}\n.uploader-ui .uploader-btn {\r\n  margin-right: 4px;\r\n  font-size: 12px;\r\n  border-radius: 3px;\r\n  color: #FFF;\r\n  background-color: #409EFF;\r\n  border-color: #409EFF;\r\n  display: inline-block;\r\n  line-height: 1;\r\n  white-space: nowrap;\n}\n.uploader-ui .uploader-list {\r\n  max-height: 440px;\r\n  overflow: auto;\r\n  overflow-x: hidden;\r\n  overflow-y: auto;\n}\n.el-header, .el-footer {\r\n   background-color: #B3C0D1;\r\n   color: #333;\r\n   text-align: center;\r\n   line-height: 60px;\n}\n，\r\n  .el-container {\r\n    height: 100%;\n}\r\n", "", {"version":3,"sources":["C:/Users/shinelon/Desktop/Course-Managing-System/frontend/src/views/docs/src/views/docs/Upload.vue"],"names":[],"mappings":";AAiMA;EACA,cAAA;EACA,oBAAA;EACA,gBAAA;EACA,6BAAA;EACA,+CAAA;UAAA,uCAAA;EACA,cAAA;EACA,aAAA;CACA;AACA;EACA,kBAAA;EACA,gBAAA;EACA,mBAAA;EACA,YAAA;EACA,0BAAA;EACA,sBAAA;EACA,sBAAA;EACA,eAAA;EACA,oBAAA;CACA;AACA;EACA,kBAAA;EACA,eAAA;EACA,mBAAA;EACA,iBAAA;CACA;AAEA;GACA,0BAAA;GACA,YAAA;GACA,mBAAA;GACA,kBAAA;CACA;AAAA;;IAEA,aAAA;CACA","file":"Upload.vue","sourcesContent":["<template>\r\n  <el-container>\r\n<!--    页面顶部栏-->\r\n    <el-header style=\"text-align: left\">    <!--文字居左-->\r\n      <div class=\"el-icon-folder\" :style=\"{Height: Height + 'px'}\">PM管理系统-文件上传中心</div>\r\n    </el-header>\r\n    <!-- 上传器 -->\r\n    <uploader\r\n      ref=\"uploader\"\r\n      :options=\"options\"\r\n      :autoStart=false\r\n      :file-status-text=\"fileStatusText\"\r\n      @file-added=\"onFileAdded\"\r\n      @file-success=\"onFileSuccess\"\r\n      @file-error=\"onFileError\"\r\n      class=\"uploader-ui\">\r\n      <uploader-drop>\r\n        <div>\r\n          <uploader-btn id=\"global-uploader-btn\" :attrs=\"attrs\" ref=\"uploadBtn\">\r\n            选择文件<i class=\"el-icon-upload el-icon--right\"></i>\r\n          </uploader-btn>\r\n        </div>\r\n      </uploader-drop>\r\n      <uploader-list></uploader-list>\r\n    </uploader>\r\n  </el-container>\r\n</template>\r\n\r\n<script>\r\nimport {ACCEPT_CONFIG} from '../../components/common/config';\r\nimport SparkMD5 from 'spark-md5';\r\nimport {mergeFile} from \"@/api/modules/uploadFile\";\r\nimport store from '../../store'\r\n\r\nexport default {\r\n  data () {\r\n    return {\r\n      options: {\r\n        //目标上传 URL，默认POST\r\n        target: `http://${process.env.BACKEND_API_HOST}`+\"/uploader/chunk\",\r\n        //分块大小(单位：字节)\r\n        chunkSize: '2048000',\r\n        //上传文件时文件内容的参数名，对应chunk里的Multipart对象名，默认对象名为file\r\n        fileParameterName: 'upfile',\r\n        //失败后最多自动重试上传次数\r\n        maxChunkRetries: 3,\r\n        //是否开启服务器分片校验，对应GET类型同名的target URL\r\n        testChunks: true,\r\n        /*\r\n        服务器分片校验函数，判断秒传及断点续传,传入的参数是Uploader.Chunk实例以及请求响应信息\r\n        reponse码是successStatuses码时，才会进入该方法\r\n        reponse码如果返回的是permanentErrors 中的状态码，不会进入该方法，直接进入onFileError函数 ，并显示上传失败\r\n        reponse码是其他状态码，不会进入该方法，正常走标准上传\r\n        checkChunkUploadedByResponse函数直接return true的话，不再调用上传接口\r\n        */\r\n        //带上请求头\r\n        headers:{'Authentication':store.state.account.token},\r\n\r\n        checkChunkUploadedByResponse: function (chunk, response_msg) {\r\n          let objMessage = JSON.parse(response_msg);\r\n          if (objMessage.skipUpload) {\r\n            return true;\r\n          }\r\n          return (objMessage.uploadedChunks || []).indexOf(chunk.offset + 1) >= 0;\r\n        }\r\n      },\r\n      attrs: {\r\n        // 接受的文件类型\r\n        accept: ACCEPT_CONFIG.getAll()\r\n      },\r\n      fileStatusText: {\r\n        success: '上传成功',\r\n        error: '上传失败',\r\n        uploading: '上传中',\r\n        paused: '暂停',\r\n        waiting: '等待上传'\r\n      },\r\n    }\r\n  },\r\n  methods: {\r\n    onFileAdded(file) {\r\n      this.computeMD5(file);\r\n    },\r\n    /*\r\n    第一个参数 rootFile 就是成功上传的文件所属的根 Uploader.File 对象，它应该包含或者等于成功上传文件；\r\n    第二个参数 file 就是当前成功的 Uploader.File 对象本身；\r\n    第三个参数就是 message 就是服务端响应内容，永远都是字符串；\r\n    第四个参数 chunk 就是 Uploader.Chunk 实例，它就是该文件的最后一个块实例，如果你想得到请求响应码的话，chunk.xhr.status就是\r\n    */\r\n    // onFileSuccess(rootFile, file, response, chunk) {\r\n    //   //refProjectId为预留字段，可关联附件所属目标，例如所属档案，所属工程等\r\n    //   file.refProjectId = \"123456789\";\r\n    //   mergeFile(file).then( responseData=> {\r\n    //     if(responseData.data.code === 415){\r\n    //       console.log(\"合并操作未成功，结果码：\"+responseData.data.code);\r\n    //     }\r\n    //   }).catch(function (error){\r\n    //     console.log(\"合并后捕获的未知异常：\"+error);\r\n    //   });\r\n    // },\r\n    // onFileError(rootFile, file, response, chunk) {\r\n    //   console.log('上传完成后异常信息：'+response);\r\n    // },\r\n\r\n    onFileSuccess(rootFile, file, response, chunk) {\r\n      //refProjectId为预留字段，可关联附件所属目标，例如所属档案，所属工程等\r\n      file.refProjectId = \"123456789\";\r\n      mergeFile(file).then( responseData=> {\r\n        if(responseData.data.code === 415){\r\n          console.log(\"合并操作未成功，结果码：\"+responseData.data.code);\r\n        }\r\n      }).catch(function (error){\r\n        console.log(\"合并后捕获的未知异常：\"+error);\r\n      });\r\n    },\r\n    onFileError(rootFile, file, response, chunk) {\r\n      console.log('上传完成后异常信息：'+response);\r\n    },\r\n\r\n    /**\r\n     * 计算md5，实现断点续传及秒传,期间文件暂停上传\r\n     * @param file\r\n     */\r\n    computeMD5(file) {\r\n      file.pause();\r\n\r\n      //单个文件的大小限制5G\r\n      let fileSizeLimit = 5 * 1024 * 1024 * 1024;\r\n      console.log(\"文件大小：\"+file.size);\r\n      console.log(\"限制大小：\"+fileSizeLimit);\r\n      if(file.size > fileSizeLimit){\r\n        this.$message({\r\n          showClose: true,\r\n          message: '文件大小不能超过5G'\r\n        });\r\n        file.cancel();\r\n      }\r\n\r\n      let fileReader = new FileReader();\r\n      let time = new Date().getTime();\r\n      let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;\r\n      let currentChunk = 0;\r\n      const chunkSize = 10 * 1024 * 1000;\r\n      let chunks = Math.ceil(file.size / chunkSize);\r\n      let spark = new SparkMD5.ArrayBuffer();\r\n      //由于计算整个文件的Md5太慢，因此采用只计算第1块文件的md5的方式\r\n      let chunkNumberMD5 = 1;\r\n\r\n      loadNext();\r\n\r\n      fileReader.onload = (e => {\r\n        spark.append(e.target.result);\r\n\r\n        if (currentChunk < chunkNumberMD5) {\r\n          loadNext();\r\n        } else {\r\n          let md5 = spark.end();\r\n          file.uniqueIdentifier = md5;\r\n          file.resume();\r\n          console.log(`MD5计算完毕：${file.name} \\nMD5：${md5} \\n分片：${chunks} 大小:${file.size} 用时：${new Date().getTime() - time} ms`);\r\n        }\r\n      });\r\n\r\n      fileReader.onerror = function () {\r\n        this.error(`文件${file.name}读取出错，请检查该文件`)\r\n        file.cancel();\r\n      };\r\n\r\n      function loadNext() {\r\n        let start = currentChunk * chunkSize;\r\n        let end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;\r\n\r\n        fileReader.readAsArrayBuffer(blobSlice.call(file.file, start, end));\r\n        currentChunk++;\r\n        console.log(\"计算第\"+currentChunk+\"块\");\r\n      }\r\n    },\r\n    close() {\r\n      this.uploader.cancel();\r\n    },\r\n    error(msg) {\r\n      this.$notify({\r\n        title: '错误',\r\n        message: msg,\r\n        type: 'error',\r\n        duration: 2000\r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style>\r\n.uploader-ui {\r\n  padding: 25px;\r\n  margin: 40px auto 0;\r\n  font-size: 20px;\r\n  font-family: Microsoft YaHei;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, .4);\r\n  height: 200px;\r\n  width: 600px;\r\n}\r\n.uploader-ui .uploader-btn {\r\n  margin-right: 4px;\r\n  font-size: 12px;\r\n  border-radius: 3px;\r\n  color: #FFF;\r\n  background-color: #409EFF;\r\n  border-color: #409EFF;\r\n  display: inline-block;\r\n  line-height: 1;\r\n  white-space: nowrap;\r\n}\r\n.uploader-ui .uploader-list {\r\n  max-height: 440px;\r\n  overflow: auto;\r\n  overflow-x: hidden;\r\n  overflow-y: auto;\r\n}\r\n\r\n.el-header, .el-footer {\r\n   background-color: #B3C0D1;\r\n   color: #333;\r\n   text-align: center;\r\n   line-height: 60px;\r\n }，\r\n  .el-container {\r\n    height: 100%;\r\n  }\r\n</style>\r\n"],"sourceRoot":""}]);

